.tz_portfolio_plus_articles {
  list-style: none;
  padding: 0;
  li {
    margin-left: -15px;
    margin-right: -15px;
    margin-bottom: 2em;
    &:before, &:after {
      content: " ";
      display: table;
      clear: both;
    }
    >div {
      float: left;
      position: relative;
      min-height: 1px;
      padding-left: 15px;
      padding-right: 15px;
    }
    .tzpp_media {
      width: 30%;
      img {
        max-width: 100%;
      }
    }
    .tzpp_content {
      width: 70%;
    }
    .item-meta {
      margin-top: 10px;
      >div {
        display: inline-block;
        font-size: 85%;
        margin-right: 10px;
      }
    }
    .title {
      margin: 0;
    }
    .description {
      margin-top: 10px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

@media (max-width: 767px) {
  .tz_portfolio_plus_articles {
    li {
      .tzpp_media {
        width: 100%;
      }
      .tzpp_content {
        width: 100%;
      }
      .title {
        margin-top: 15px;
      }
    }

  }
}